let that;
export default {
	data() {
		return {
			isok: false,
			selectext: '',
			goodStatus: ['全部', '已上架', '已下架'],
			serviceState: '',
			isSelect: false,
			putUp: 'color:#0E5E56;border:1px solid #0E5E56',
			putDown: 'color: #D92B2B;border:1px solid #D92B2B',
			modelList: [
				// { name: '{模板可编辑}基础火化服务', price: '450', num: 1, putActive: false,url:'../addService/IncinerateModel/IncinerateModel'},
				{ name: '{模板可编辑}接送服务', num: 2, price: '200', putActive: false,url:'../addService/pickUpModel/pickUpModel' },
				{ name: '{模板可编辑}冷冻服务', price: '200', num: 3, putActive: false,url:'../addService/usualModel/freezeModel'},
				{ name: '{模板可编辑}清洁服务', price: '200', num: 4, putActive: false,url:'../addService/usualModel/freezeModel',type:'clean' },
				{ name: '{模板可编辑}仪式服务', price: '200', num: 5, putActive: false,url:'../addService/riteModel/riteModel' }
			],
			servicesInfo: ''
		};
	},
	onLoad() {
		that = this;
		this.init();
	},
	methods: {
		addService() {
			uni.navigateTo({
				url: '../addService/addService'
			});
			uni.navigateTo({
				url:'../addService/riteModel/riteModel'
			})
		},
		btnColor(e) {
			console.log(e);
		},
		init() {
			this.$api.showLoading();
			this.selectAllService();
			this.$api.showLoading();
		},
		toModelPage(item){
			uni.navigateTo({
				url:item.url+'?type='+item.type,
			})
		},
		selectAllService() {
			this.storeId = uni.getStorageSync('storeId');
			let storeId = this.storeId;
			console.log(this.storeId);
			this.$api
				.selectServiceByStoreAndState({
					storeId: storeId,
					serviceState: this.serviceState || ''
				})
				.then(data => {
					console.log(data);
					if ((data.data.code = 100000)) {
						this.servicesInfo = data.data.servicesInfo;
						this.isok = true;
						uni.hideLoading();
					}
				});
		},
		selectService(e) {
			if (e) {
				this.selectext = e;
				setTimeout(() => {
					this.isSelect = false;
				}, 100);
				//  重新
				if (e == '已上架') {
					this.serviceState = 1;
				} else if (e == '已下架') {
					this.serviceState = 2;
				} else if (e == '全部') {
					this.serviceState = '';
				}
				this.selectAllService();
			}
		},
		//  上架商品
		putService(e) {
			this.lowerShelfService(e);
			if (e.serviceState == 2) {
				uni.showModal({
					content: '确定要上架此服务吗',
					success: re => {
						if (re.confirm) {
							this.$api
								.upperShelfService({
									serviceId: e.serviceId
								})
								.then(data => {
									console.log(data);
									if ((data.data.code = 100000)) {
										this.$api.showToast('上架成功');
										this.selectAllService();
									}
								});
						}
					}
				});
			}
			console.log(e);
			e.putActive = !e.putActive;
		},
		// 下架服务
		lowerShelfService(e) {
			if (e.serviceState == 1) {
				uni.showModal({
					content: '确定要下架此服务吗',
					success: re => {
						if (re.confirm) {
							this.$api
								.lowerShelfService({
									serviceId: e.serviceId
								})
								.then(data => {
									console.log(data);
									if ((data.data.code = 100000)) {
										this.$api.showToast('下架成功');
										this.selectAllService();
									}
								});
						}
					}
				});
			}
		},
		deleteService(e) {
			// 删除模板
			uni.showModal({
				content: '确定要删除此服务吗',
				success: re => {
					if (re.confirm) {
						this.$api
							.deleteService({
								serviceId: e
							})
							.then(data => {
								if (data.data.code == 100000) {
									this.$api.showToast('删除成功');
									this.selectAllService();
								}
							});
					}
				}
			});
		},
		// 编辑服务
		editor(e) {
			console.log(e);
			//  每次编辑自动更新 需要修改的 商品
			uni.setStorageSync('editService', e);
			if (e) {
				uni.navigateTo({
					url: '../addService/addService?type=update'
				});
			}
		}
	}
};
